.container {
  position: relative;
}

/* header */
.menu {
  text-align: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  border-top: 1px solid #eee;
}
.menu li {
  height: 40px;
  line-height: 40px;
  display: inline-block;
}


.fund-container {
  margin: 20px;
}
.negative-income {
  color: green;
}
.positive-income {
  color: red;
}
.income-rate {
  display: inline-block;
  width: 90px;
  text-align: right;
}
.fund-name {
  padding-left: 20px;
}
.list-header {
  margin: 20px 0 0 0;
  font-weight: 600;
}
.have-tag {
  display: inline-block;
  width: 30px;
  color: red;
}
.operation-container {
  margin-top: 10px;
}
.operation-container button {
  margin-right: 10px;
  cursor: pointer;
}
.red {
  font-size: 12px;
  color: red;
  margin: 0 5px;
}
.list-container {
  overflow: auto;
}
.list-content {
  width: 1200px;
}
.total-all {
  position: absolute;
  top: 10px;
  right: 10px;
  opacity: 0;
  cursor: pointer;
}
.swtich-number {
  top: 40px;
}